import React, { useState, useEffect } from 'react';



function CreatePreset() {
    const pages =
    {
        DRILLS: "DRILLS",
        TIME: "TIME",
        SETS: "SETS",
    }

    const inputs = ["00", "01", "02", "03", "04", "05", "06", "07", "08", "09", "10", "11", "12", "13", "14"]

    const [currentPage, setCurrentPage] = useState(pages.DRILLS);

    function handlePageClicked(event) {
        setCurrentPage(event.target.value)
    }


    function handleChange(event) {
        var _id = event.target.id
        var _id_int = parseInt(_id.slice(10, 12)) + 1
        let _id_string = _id_int.toString()
        if (_id_int < 10) {
            _id_string = "0" + _id_int.toString()
        }
        if (_id_int == 15) {
            return
        }
        document.getElementById("set-input-" + _id_string).focus()
    }

    function handleFocused(event) {
        document.getElementById(event.target.id).value = ""
    }

    function getCurrentPage() {
        if (currentPage === pages.DRILLS) {
            return (
                <div className="input-grid">
                    {inputs.map((item) => (
                        <input key={item} id={"set-input-" + item} type="text" className="input-grid-item" placeholder="-" maxLength="1" onChange={handleChange} onFocus={handleFocused} />
                    ))}
                </div>
            )
        }
        if (currentPage === pages.TIME) {
            return (
                <div>
                    <div className="input-two-grid">
                        <input type="number" id="min-time" className="input-grid-item" placeholder="Minimum Seconds" />
                        <p >-</p>
                        <input type="number" id="max-time" className="input-grid-item" placeholder="Maximum Seconds" />
                    </div>
                    <p className="big-description">Set the maximum and minimum time between drills</p>
                </div>
            )
        }
        if (currentPage === pages.SETS) {
            return (
                <div>
                    <div className="input-two-grid">
                        <input type="number" id="work-time" className="input-grid-item" placeholder="Work Time (Seconds)" />
                        <p >-</p>
                        <input type="number" id="break-time" className="input-grid-item" placeholder="Break Time (Seconds)" />
                    </div>
                    <p className="big-description">Set how long sets and breaks should be</p>
                </div>
            )
        }
    }


    return (
        <div>
            <button className="tab" value={pages.DRILLS} onClick={handlePageClicked}>DRILLS</button>
            <button className="tab" value={pages.TIME} onClick={handlePageClicked}>TIME</button>
            <button className="tab" value={pages.SETS} onClick={handlePageClicked}>SETS</button>
            {getCurrentPage()}
        </div>
    )
}

export default CreatePreset
